import React from 'react';
import ReactDOM from 'react-dom';
import { Link } from 'react-router';

class Unexchange extends React.Component {
  constructor(props) {
    super();
    this.state = {
      uname:'Tom'
    }
  }

  render() {
    return (
      <div>
        <span>用户id:{this.state.uname}</span>
        <ol>
          <li>礼物1</li>
          <li>礼物2</li>
          <li>礼物3</li>
        </ol>
        <p><Link to="/demo/single.html/unreceive">点击兑换</Link></p>
      </div>
    );
  }
}

Unexchange.defaultProps = {
  uname:''
}

Unexchange.propTypes = {
  uname: React.PropTypes.string
}

module.exports = Unexchange;



