import React from 'react';
import ReactDOM from 'react-dom';
import { Router, Route, IndexRoute, Link, browserHistory } from 'react-router';
import Home from './home';
import Unexchange from './unexchange';
import Unreceive from './unreceive';
import Done from './done';

class Single extends React.Component {
  constructor(props) {
    super();
  }

  render() {
    return (
      <div>
        {this.props.children}
      </div>
    );
  }
}

Single.defaultProps = {

}

Single.propTypes = {

}

ReactDOM.render((
  <Router history={browserHistory}>
    <Router path="/demo/single.html" component={Single}>
      <IndexRoute component={Home}/>
      <Route path="/demo/single.html/unexchange" component={Unexchange}/>
      <Route path="/demo/single.html/unreceive" component={Unreceive}/>
      <Route path="/demo/single.html/done" component={Done}/>
    </Router>
  </Router>
), document.getElementById('example'));



